                                        label="ຫາວັນທີ"
                                        readonly
                                        outlined
                                        v-bind="attrs"
                                        v-on="on"
                                        dense
                                        clearable
                                ></v-text-field>
                            </template>
                            <v-date-picker v-model="end_date"></v-date-picker>
                        </v-menu>
                    </v-col>
                    <v-col>
                        <v-autocomplete
                                outlined
