                                item-text="name"
                                return-object
                                label="ເມືອງ"
                                clearable
                        ></v-autocomplete>
                    </v-col>
                    <v-col>
                        <v-autocomplete
                                outlined
                                dense
                                :items="selectedDistrict.villages"
                                v-model="selectedVillage"
                                item-text="name"
                                item-value="id"
                                label="ບ້ານ"
                                multiple
                                clearable
                        ></v-autocomplete>
                    </v-col>
                </v-row>

                <v-row>
                    <VillageDetail
                            :selectedVillage="selectedVillage"
                            v-model="selectedDetails"
                    ></VillageDetail>
                </v-row>

                <v-row>
                    <v-col>
                        <v-select
                                outlined
                                dense
                                :items="customerTypes"
                                v-model="selectedCustomerType"
                                item-text="name_la"
                                item-value="name"
                                label="ເລືອກປະເພດລູກຄ້າ"
                                clearable
                        ></v-select>
                    </v-col>
                    <v-col v-if="selectedCustomerType == 'company'">
                        <v-select
                                outlined
                                dense
                                :items="comapnyTypes"
                                v-model="selectedComapnyType"
                                item-text="la"
                                item-value="en"
                                label="ເລືອກປະເພດບໍລິການ"
                                clearable
                        ></v-select>
                    </v-col>
                    <v-col v-if="selectedCustomerType == 'home'">
                        <v-select
                                v-model="filteredPackage"
                                :items="packageList"
                                item-text="name"
                                item-value="id"
                                label="ເລືອກແພັກເກດ"
                                outlined
                                dense
                        ></v-select>
                    </v-col>
                </v-row>

                <v-row>
                    <v-col>
                        <v-text-field
                                outlined
                                dense
                                clearable
                                prepend-inner-icon="mdi-folder-key"
                                label="ໄອດີລູກຄ້າ"
                                type="text"
                                v-model="customer_id"
                                @keyup.enter="fetchData()"
                        >
                        </v-text-field>
                    </v-col>

                    <v-col>
                        <v-text-field
                                outlined
                                dense
                                clearable
                                prepend-inner-icon="mdi-phone"
                                label="ໂທລະສັບ"
                                type="text"
                                v-model="phone"
                                @keyup.enter="fetchData()"
                        >
                        </v-text-field>
                    </v-col>

                    <v-col>
                        <v-text-field
                                outlined
                                dense
                                clearable
                                prepend-inner-icon="mdi-account"
                                label="ຊື່"
                                type="text"
                                v-model="search"
                                @keyup.enter="fetchData()"
                        >
                        </v-text-field>
                    </v-col>

                    <v-col>
                        <v-btn style="width:100%" color="green" dark @click="fetchData()"
                        >ຄົ້ນຫາ
                        </v-btn
                        >
                    </v-col>
                </v-row>
            </v-col>

        </v-row>

        <v-row>
            <v-col>
                <v-row>
                    <v-col>
                        <v-simple-table
                                style="white-space: nowrap"
                        >
                            <thead>
                            <tr>
                                <td v-for="header in headers" :key="header.text">{{ header.text }}</td>
                            </tr>
                            </thead>
                            <tbody>
                            <template v-for="customer in customers">
                                <tr :key="customer.id">
                                    <td :rowspan="customer.user.billings.length ? customer.user.billings.length : 1">
                                        <v-row>
                                            <a @click="viewCustomerDetail(customer)" href="#"
                                               class="font-weight-bold text--accent-3 ml-6">{{
                                                    customer.customer_id
                                                }}</a>
                                        </v-row>
                                        <v-row>
                                            <p class="text-large ml-6"
                                               :style=" customer.user.billings_sum_total - customer.user.billings_paid_sum_total <= 0 ? 'color: green' : 'color: orange'">
                                                {{ customer.full_name }}</p>
                                        </v-row>
                                        <v-row>
                                            <v-icon small class="ml-8 mr-1"> mdi-map-marker</v-icon>
                                            {{ customer.village.name }},
                                            {{ customer.village.district.name }}
                                        </v-row>
                                        <v-row>
                                            <v-icon small class="ml-8 mr-1"> mdi-phone</v-icon>
                                            {{ customer.user.phone }}
                                        </v-row>
                                    </td>
                                    <td :rowspan="customer.user.billings.length ? customer.user.billings.length : 1">
                                       <v-icon> {{customer.customer_type == 'home' ? 'mdi-account-group' : 'mdi-office-building'}}</v-icon> {{ customer.cost_by_la }}
                                    </td>
                                    <td :rowspan="customer.user.billings.length ? customer.user.billings.length : 1">
                                        <v-list>
                                            <v-list-item>
                                                <v-list-item-icon>
                                                    <v-chip small color="primary" dark>
                                                        ລວມ
                                                    </v-chip>
                                                </v-list-item-icon>
                                                <v-list-item-content class="text-large">
                                                    {{ Intl.NumberFormat().format(customer.user.billings_sum_total) }}
                                                    ({{ customer.user.billings_count }} ບິນ)
                                                </v-list-item-content>
                                            </v-list-item>

                                            <v-list-item>
                                                <v-list-item-icon>
                                                    <v-chip small color="success" dark>
                                                        ຈ່າຍແລ້ວ
                                                    </v-chip>
                                                </v-list-item-icon>
                                                <v-list-item-content class="text-large">
                                                    {{
                                                        Intl.NumberFormat().format(customer.user.billings_paid_sum_total)
                                                    }}
                                                    ({{ customer.user.billings_paid_count }} ບິນ)
                                                </v-list-item-content>
                                            </v-list-item>

                                            <v-list-item>
                                                <v-list-item-icon>
                                                    <v-chip small color="warning" dark>
                                                        ຄົງຄ້າງ
                                                    </v-chip>
                                                </v-list-item-icon>
                                                <v-list-item-content class="text-large">
                                                    {{
                                                        Intl.NumberFormat().format(customer.user.billings_unpaid_sum_total)
                                                    }}
                                                    ({{ customer.user.billings_unpaid_count }} ບິນ)
                                                </v-list-item-content>
                                            </v-list-item>
                                        </v-list>

                                    </td>

                                    <td>
                                        <v-row>
                                            <a @click="openBilling(customer.user.billings[0].id)" href="#"
                                               class="font-weight-bold text--accent-3">{{
                                                    customer.user.billings[0].billing_display_id
                                                }}</a>
                                        </v-row>
                                        <v-row>
                                            <div class="">{{ customer.user.billings[0].content }}</div>
                                        </v-row>
                                    </td>
                                    <td>{{ Intl.NumberFormat().format(customer.user.billings[0].total) }}</td>
                                    <td>
                                        <v-chip :color="getBgColorFn(customer.user.billings[0].status)" dark>
                                            {{ customer.user.billings[0].status_la }}
                                        </v-chip>
                                    </td>
                                    <td>{{ customer.user.billings[0].payment_method_la }}</td>
                                    <td>{{
                                            customer.user.billings[0].paided_by ? customer.user.billings[0].paided_by.name : ''
                                        }}
                                    </td>
                                    <td>{{ customer.user.billings[0].created_at }}</td>
                                    <td>{{ customer.user.billings[0].approved_at }}</td>
                                    <td>{{ customer.user.billings[0].paided_at }}</td>
                                    <td>{{ customer.user.billings[0].confirmed_payment_at }}</td>
                                </tr>
                                <tr v-for="billing in customer.user.billings.slice(1)" :key="billing.id">
                                    <td>
                                        <v-row>
                                            <a @click="openBilling(billing.id)" href="#"
                                               class="font-weight-bold text--accent-3">{{
                                                    billing.billing_display_id
                                                }}</a>
                                        </v-row>
                                        <v-row>
                                            <p>{{ billing.content }}</p>
                                        </v-row>
                                    </td>
                                    <td>{{ Intl.NumberFormat().format(billing.total) }}</td>
                                    <td>
                                        <v-chip :color="getBgColorFn(billing.status)" dark>
                                            {{ billing.status_la }}
                                        </v-chip>
                                    </td>
                                    <td>{{ billing.payment_method_la }}</td>
                                    <td>{{ billing.paided_by ? billing.paided_by.name : '' }}</td>
                                    <td>{{ billing.created_at }}</td>
                                    <td>{{ billing.approved_at }}</td>
                                    <td>{{ billing.paided_at }}</td>
                                    <td>{{ billing.confirmed_payment_at }}</td>
                                </tr>
                            </template>
                            </tbody>
                        </v-simple-table>
                        <template>
                            <Pagination :pagination="pagination" :offset="offset"
                                        @paginate="fetchData()"></Pagination>
                        </template>
                    </v-col>
                </v-row>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import queryOptions from "../../Helpers/queryOption";
import {getBgColor} from "@/Helpers/BillingStatus"
import {getCompanyCostBy, concatPackage} from "@/Helpers/Customer";
import VillageDetail from "@/components/select/VillageDetail"
//import RowSection from "../../components/card/RowSection.vue";

export default {
    title() {
        return `Vientiane Waste Co-Dev|Calendar`;
    },
    components: {
//        RowSection,
        VillageDetail
    },
    data() {
        return {
            firstLoad: true,
            filteredPackage: "",
            selectedCustomerType: "",
            selectedComapnyType: "",
            comapnyTypes: getCompanyCostBy,
            customerTypes: [
                {
                    name: "",
                    name_la: "ທັງໝົດ",
                },
                {
                    name_la: "ຄົວເຮືອນ",
                    name: "home",
                },
                {
                    name_la: "ຫົວໜ່ວຍທຸລະກິດ",
                    name: "company",
                },
            ],
            start_date: "",
            end_date: "",
            selected_month:
                this.$route.query.month || new Date().toISOString().substr(0, 7),
            month_menu: false,
            start_menu: false,
            end_menu: false,
            districts: [],
            selectedDistrict: "",
            districtLoaded: false,
            villages: [],
            selectedVillage: [],
            selectedDetails: [],
            selectedStatus: [],

            search: "",
            phone: "",
            customer_id: "",

            loading: false,
            customerId: "",
            pagination: {},
            per_page: 100,
            offset: 12,

            customers: [],
            headers: [
                {text: 'ລູກຄ້າ', value: 'full_name', align: 'center', divider: true},
                {text: 'ປະເພດບໍລິການ', value: 'cost_by_la'},
                {text: 'ຍອດລວມ', value: ''},
                {text: 'ໄອດີບິນ', value: 'billings_id'},
                {text: 'ຈຳນວນ', value: 'billings_total'},
                {text: 'ສະຖານະຈ່າຍ', value: 'billings_status'},
                {text: 'ຈ່າຍດ້ວຍ', value: 'billings_paymet_method'},
                {text: 'ຜູ້ຈ່າຍບິນ', value: 'billings_paid_by'},
                {text: 'ເວລາສ້າງບິນ', value: 'billings_created_at'},
                {text: 'ເວລາອະນຸມັດບິນ', value: 'billings_approved_at'},
                {text: 'ເວລາຈ່າຍບິນ', value: 'billings_paid_at'},
                {text: 'ເວລາຢືນຢັນການຈ່າຍ', value: 'billings_confirm_at'},
            ]
        };
    },
    methods: {
        viewCustomerDetail(item) {
            const routeName = item.customer_type == 'home'
                ? 'ViewClient'
                : 'ViewCompanyDetail'

            const options = {
                name: routeName,
                params: queryOptions([
                    {
                        id: item.id,
                    }
                ]),
            }

            this.openRoute(options)
        },
        getBgColorFn(status) {
            return getBgColor(status)
        },
        async fetchPackage() {
            const res = await this.$axios
                .get("package")
                .catch(() => {
                });
            if (res.data.code == 200) {
                this.packages = res.data.data;
            }
        },
        async fetchVillage() {
            const res = await this.$axios
                .get("info/district/" + this.selectedDistrict + "/village")
                .catch(() => {
                });

            if (res.data.code == 200) {
                this.villages = res.data.data;
            }
        },
        async fetchAddress() {
            this.districtLoaded = false
            const res = await this.$axios
                .get("info/address", {params: {filter: "ນະຄອນຫລວງວຽງຈັນ", with_village: true}})
                .catch(() => {
                });

            if (res.data.code == 200) {
                this.address = res.data.data;
                for (const addressItem of this.address) {
                    this.districts = addressItem.districts;
                }
            }
            this.districtLoaded = true

        },
        async fetchData() {
            this.$store.commit("Loading_State", true);
            const res = await this.$axios.get('v2/report-billing-for-customer', {
                params: queryOptions([
                    {order_by: 'newest'},
                    {without_month_info: true},
                    {has_billing_only: true},
                    {customer_id: this.customer_id},
                    {phone: this.phone},
                    {customer_type: this.selectedCustomerType},
                    {package_id: this.package_id},
                    {cost_by: this.selectedComapnyType},
                    {villages: this.selectedVillage},
                    {district_id: this.selectedDistrict},
                    {village_details: this.selectedDetails},
                    {date_from: this.start_date},
                    {date_to: this.end_date},
                    {filter: this.search},
                    {per_page: this.per_page},
                    {page: this.pagination.current_page}
                ])
            }).catch((err) => console.log(err))
            this.$store.commit("Loading_State", false);

            this.customers = res.data.data.data
            this.pagination = res.data.data.pagination
        },
      setParam(){
       if(this.$route.query.customer_id) this.customer_id = this.$route.query.customer_id
       if(this.$route.query.phone) this.phone = this.$route.query.phone
       if(this.$route.query.selectedCustomerType) this.selectedCustomerType = this.$route.query.selectedCustomerType
       if(this.$route.query.package_id) this.package_id = this.$route.query.package_id
       if(this.$route.query.selectedComapnyType) this.selectedComapnyType = this.$route.query.selectedComapnyType
       if(this.$route.query.selectedVillage) this.selectedVillage = this.$route.query.selectedVillage
       if(this.$route.query.selectedDistrict) this.selectedDistrict = this.$route.query.selectedDistrict
       if(this.$route.query.selectedDetails) this.selectedDetails = this.$route.query.selectedDetails
       if(this.$route.query.start_date) this.start_date = this.$route.query.start_date
       if(this.$route.query.end_date) this.end_date = this.$route.query.end_date
       if(this.$route.query.search) this.search = this.$route.query.search
      },
        openBilling(id) {
            const options = {
                name: "billing-detail",
                params: queryOptions([
                    {
                        id: id,
                    }
                ]),
            }

            this.openRoute(options)
        },
        openRoute(options) {
            const routeData = this.$router.resolve({
                ...options
            });

            window.open(routeData.href);
        },
        backPrevios() {
            this.$router.go(-1);
            // this.$router.push({
            //   name: "PlanCalendar",
            //   params: { id },
            // });
            // this.$router.push({
            //   name: "PlanCalendar",
            //   params: { id: this.data.plan_month_id },
            // });
        },
        // fetchDataPlanMonth() {
        //   this.$axios
        //     .get(
        //       "plan-month/" +
        //         this.$route.params.driverId +
        //         "/plan-calendar/" +
        //         this.$route.params.id
        //     )
        //     .then((res) => {
        //       if (res.data.code == 200) {
        //         setTimeout(() => {
        //           this.data = res.data.data;
        //           console.log(this.data);
        //         }, 100);
        //       }
        //     })
        //     .catch(() => {});
        // },
    },
    async created() {
      this.setParam()
        await this.fetchAddress()
        await this.fetchPackage()
        this.fetchData()
    },
    computed: {
        packageList() {
            return concatPackage(this.packages);
        },
    },
    watch: {},
};
</script>

<style>
.mx-input {
    height: 40px !important;
}
</style>
